<template>
  <v-container grid-list-xs>
    <v-overlay :value="overlay">
      <v-progress-circular
        color="primary"
        indeterminate
        size="50"
      ></v-progress-circular>
      <v-btn icon @click="overlay = false">
        <v-icon>mdi-close</v-icon>
      </v-btn>
    </v-overlay>
    <v-dialog
      v-model="addDialog"
      width="500"
    >
      <v-card>
        <v-card-title class="text-h5 grey lighten-2">
          Confirm
        </v-card-title>

        <v-card-text>
          If you proceed this record will be added into the database, proceed?.
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-btn
            color="primary"
            @click="addDialog = false"
          >
            Cancel
          </v-btn>
          <v-spacer></v-spacer>
          <v-btn
            color="error"
            text
            @click="save"
          >
            Yes
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-card
      class="mx-auto"
      max-width="700"
      outlined
      elevation="0"
    >
      <v-card-title>Add new record</v-card-title>
      <v-card-text class="px-12 pt-6">
        <v-radio-group
          v-model="idType"
          row
        >
          <v-radio
            label="OMANG"
            value="OMANG"
          ></v-radio>
          <v-radio
            label="Passport"
            value="Passport"
          ></v-radio>
        </v-radio-group>
        <v-text-field
          v-if="idType"
          :label="'Enter ' + idType + ' Number*'"
          v-model="idNum" 
          outlined 
          hide-details="auto" 
          :rules="idnumrules"
          dense
          @keyup.native.enter="search"
        />
      </v-card-text>
      <v-progress-linear
        v-if="searching"
        color="deep-purple accent-4"
        indeterminate
        rounded
        height="6"
      ></v-progress-linear>
      <v-alert
        dense
        outlined
        type="error"
        v-if="displaySearchResults && reason"
      >
        Cant add practitioner with this identifier as {{ reason }}
      </v-alert>
      <v-card v-if="displaySearchResults">
        <v-toolbar
          color="primary"
          dark
          flat
        >

          <v-toolbar-title>Search Results</v-toolbar-title>

          <template v-slot:extension>
            <v-tabs centered v-model="tab">
              <v-tabs-slider color="yellow"></v-tabs-slider>

              <v-tab key="hie">
                <v-badge
                  v-if="hierecords.length"
                  color="green"
                  :content="hierecords.length"
                >
                  HIE
                </v-badge>
                <v-badge
                  v-else
                  color="error"
                  content="0"
                >
                  HIE
                </v-badge>
              </v-tab>
              <v-tab key="ihris">
                <v-badge
                  v-if="ihrisrecords.length"
                  color="error"
                  :content="ihrisrecords.length"
                >
                  iHRIS
                </v-badge>
                <v-badge
                  v-else
                  color="green"
                  content="0"
                >
                  iHRIS
                </v-badge>
              </v-tab>
            </v-tabs>
          </template>
        </v-toolbar>

        <v-tabs-items v-model="tab">
          <v-tab-item key="hie">
            <v-card flat>
              <v-card-text v-if="!hierecords.length">
                No records in HIE found
              </v-card-text>
              <v-card-text>
                <v-data-iterator
                  :items="hierecords"
                  items-per-page.sync="6"
                  hide-default-footer
                >

                  <template v-slot:default="props">
                    <v-row>
                      <v-col
                        v-for="item in props.items"
                        :key="item.id"
                        cols="12"
                        sm="6"
                        md="4"
                        lg="6"
                      >
                        <v-card>
                          <v-card-title class="subheading font-weight-bold">
                            {{ item.family }} {{ item.given }}
                          </v-card-title>

                          <v-divider></v-divider>

                          <v-list dense>
                            <v-list-item>
                              <v-list-item-content>Surname:</v-list-item-content>
                              <v-list-item-content class="align-end">
                                {{ item.family }}
                              </v-list-item-content>
                            </v-list-item>

                            <v-list-item>
                              <v-list-item-content>Firstname(s):</v-list-item-content>
                              <v-list-item-content class="align-end">
                                {{ item.given }}
                              </v-list-item-content>
                            </v-list-item>

                            <v-list-item>
                              <v-list-item-content>Gender:</v-list-item-content>
                              <v-list-item-content class="align-end">
                                {{ item.gender }}
                              </v-list-item-content>
                            </v-list-item>

                            <v-list-item>
                              <v-list-item-content>Date of Birth:</v-list-item-content>
                              <v-list-item-content class="align-end">
                                {{ item.dob }}
                              </v-list-item-content>
                            </v-list-item>

                            <v-list-item>
                              <v-list-item-content>Marital Status:</v-list-item-content>
                              <v-list-item-content class="align-end">
                                {{ item.marital }}
                              </v-list-item-content>
                            </v-list-item>
                          </v-list>
                        </v-card>
                      </v-col>
                    </v-row>
                  </template>
                </v-data-iterator>
              </v-card-text>
            </v-card>
          </v-tab-item>
          <v-tab-item key="ihris">
            <v-card flat>
              <v-card-text v-if="!ihrisrecords.length">
                No records in iHRIS found
              </v-card-text>
              <v-card-text v-else>
                <v-data-iterator
                  :items="ihrisrecords"
                  items-per-page.sync="2"
                  hide-default-footer
                >

                  <template v-slot:default="props">
                    <v-row>
                      <v-col
                        v-for="item in props.items"
                        :key="item.id"
                        cols="12"
                        sm="6"
                        md="4"
                        lg="6"
                      >
                        <v-card>
                          <v-card-title class="subheading font-weight-bold">
                            <a :href="'/resource/view/bwpractitioner/' + item.id">{{ item.family }} {{ item.given }}</a>
                          </v-card-title>

                          <v-divider></v-divider>

                          <v-list dense>
                            <v-list-item>
                              <v-list-item-content>Surname:</v-list-item-content>
                              <v-list-item-content class="align-end">
                                {{ item.family }}
                              </v-list-item-content>
                            </v-list-item>

                            <v-list-item>
                              <v-list-item-content>Firstname(s):</v-list-item-content>
                              <v-list-item-content class="align-end">
                                {{ item.given }}
                              </v-list-item-content>
                            </v-list-item>

                            <v-list-item>
                              <v-list-item-content>Gender:</v-list-item-content>
                              <v-list-item-content class="align-end">
                                {{ item.gender }}
                              </v-list-item-content>
                            </v-list-item>

                            <v-list-item>
                              <v-list-item-content>Date of Birth:</v-list-item-content>
                              <v-list-item-content class="align-end">
                                {{ item.dob }}
                              </v-list-item-content>
                            </v-list-item>
                          </v-list>
                        </v-card>
                      </v-col>
                    </v-row>
                  </template>
                </v-data-iterator>
              </v-card-text>
            </v-card>
          </v-tab-item>
        </v-tabs-items>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="success" :disabled="ihrisrecords.length > 0 || !hierecords.length" @click="addDialog = true">Proceed</v-btn>
        </v-card-actions>
      </v-card>
    </v-card>
  </v-container>
</template>
<script>
export default {
  data() {
    return {
      overlay: false,
      addDialog: false,
      searching: false,
      displaySearchResults: false,
      reason: "",
      tab: null,
      idType: "",
      idNum: "",
      errors: {
        idtype: [],
        idnum: []
      },
      records: [],
      hierecords: [],
      ihrisrecords: [],
      maritalStatus: {
        S: "Never Married",
        W: "Widowed",
        U: "unmarried",
        T: "Domestic partner",
        P: "Polygamous",
        C: "Common Law",
        M: "Married",
        L: "Legally Separated",
        I: "Interlocutory",
        D: "Divorced",
        A: "Annulled"
      }
    }
  },
  methods: {
    search() {
      this.records = []
      this.hierecords = []
      this.ihrisrecords = []
      this.searching = true
      this.reason = ""
      fetch("/site/bw/search/" + this.idType + "/" + this.idNum).then(async(response) => {
        response.json().then(async (data) => {
          this.searching = false
          this.displaySearchResults = true
          if(data.hie && data.hie.length) {
            this.records = data.hie
            for(let hie of data.hie) {
              hie = hie.resource
              let record = {
                id: hie.id
              }
              if(hie.name && hie.name.length) {
                record.family = hie.name[0].family
                if(hie.name[0].given && hie.name[0].given.length) {
                  record.given = hie.name[0].given.join(", ")
                }
              }
              if(hie.gender === "female") {
                record.gender = "Female"
              } else if(hie.gender === "male") {
                record.gender = "Male"
              }
              if(hie.birthDate) {
                record.dob = hie.birthDate
              }
              if(hie.maritalStatus && hie.maritalStatus.coding && hie.maritalStatus.coding.length) {
                let code = hie.maritalStatus.coding[0].code
                let marital = this.maritalStatus[code]
                record.marital = marital
              }
              this.hierecords.push(record)
            }
          } else {
            this.reason = "no records of this identifier in HIE"
          }
          if(data.ihris && data.ihris.length) {
            if(this.reason) {
              this.reason += " and "
            }
            this.reason += "practitioner of this identifier exists in iHRIS"
            for(let ihris of data.ihris) {
              ihris = ihris.resource
              let record = {
                id: ihris.id
              }
              if(ihris.name && ihris.name.length) {
                record.family = ihris.name[0].family
                if(ihris.name[0].given && ihris.name[0].given.length) {
                  record.given = ihris.name[0].given.join(", ")
                }
              }
              if(ihris.gender === "female") {
                record.gender = "Female"
              } else if(ihris.gender === "male") {
                record.gender = "Male"
              }
              if(ihris.birthDate) {
                record.dob = ihris.birthDate
              }
              this.ihrisrecords.push(record)
            }
          }
        }).catch(() => {
          this.searching = false
          this.displaySearchResults = true
        })
      }).catch(() => {
        this.searching = false
        this.displaySearchResults = true
      })
    },
    save() {
      this.addDialog = false
      this.overlay = true
      let resource = this.records[0].resource
      resource.active = true
      delete resource.meta
      delete resource.maritalStatus
      delete resource.id
      delete resource.address
      delete resource.link
      delete resource.text
      let omang = resource.identifier.find((id) => {
        return id.system === "http://moh.bw.org/ext/identifier/omang"
      })
      let passport = resource.identifier.find((id) => {
        return id.system === "http://moh.bw.org/ext/identifier/passport"
      })
      if((omang && omang.value) || (passport && passport.value)) {
        resource.identifier = []
      }
      if(omang && omang.value) {
        resource.identifier.push({
          extension: [ {
            url: "http://ihris.org/fhir/StructureDefinition/ihris-country-issued",
            valueCoding: {
              system: "urn:iso:std:iso:3166",
              code: "BW"
            }
          } ],
          type: {
            coding: [ {
              system: "http://ihris.org/fhir/CodeSystem/ihris-id-type-codesystem",
              code: "omang",
              display: "OMANG"
            } ],
            text: "OMANG"
          },
          system: "http://moh.gov.bw",
          value: omang.value
        })
        resource.extension = [{
          url: "http://ihris.org/fhir/StructureDefinition/ihris-nationality",
          valueCoding: {
            system: "urn:iso:std:iso:3166",
            code: "BW",
            display: "Botswana"
          }
        }, {
          url: "http://ihris.org/fhir/StructureDefinition/ihris-practitioner-immigration-status",
          valueCoding: {
            system: "http://ihris.org/fhir/CodeSystem/ihris-immigration-status-codesystem",
            code: "citizen",
            display: "Citizen"
          }
        }]
      }
      if(passport && passport.value) {
        resource.identifier.push({
          extension: [ {
            url: "http://ihris.org/fhir/StructureDefinition/ihris-country-issued",
            valueCoding: {
              system: "urn:iso:std:iso:3166",
              code: "BW"
            }
          } ],
          type: {
            coding: [ {
              system: "http://ihris.org/fhir/CodeSystem/ihris-id-type-codesystem",
              code: "passport",
              display: "Passport"
            } ],
            text: "Passport"
          },
          system: "http://moh.gov.bw",
          value: passport.value
        })
      }
      resource.meta = {}
      resource.meta.profile = ["http://ihris.org/fhir/StructureDefinition/bw-practitioner"]
      resource.resourceType = "Practitioner"
      let opts = {
        method: "POST",
        headers: {
          "Content-Type": "application/fhir+json"
        },
        redirect: 'manual',
        body: JSON.stringify(resource)
      }
      fetch("/fhir/Practitioner", opts).then((response) => {
        this.overlay = false;
        if(response.status === 201) {
          this.$store.commit("setMessage", {
            type: "success",
            text: "Practitioner added successfully",
          });
        } else {
          this.$store.commit("setMessage", {
            type: "error",
            text: "Failed to add practitioner."
          });
        }
        response.json().then((resp) => {
          this.$router.push('/questionnaire/bw-primary/bwpractitioner/' + resp.id)
        })
      }).catch(() => {
        this.overlay = false;
        this.$store.commit("setMessage", {
          type: "error",
          text: "Failed to add practitioner."
        });
      })
    }
  },
  computed: {
    idnumrules() {
      return [
        (v) => {
          if(!v) {
            this.errors.idnum = ["ID Number is required"]
            return "ID Number is required"
          }
          return true
        }
      ]
    }
  }
}
</script>